angular.module('ballsbouncingapp').controller('BallBounceController', [function () {
    var canvas = document.getElementById("myCanvas");
    var pause = true;
    var balls = [];
    var isCanvasSupported = false;
    var context = '';
    if (canvas.getContext){
        isCanvasSupported = true;
        context = canvas.getContext('2d');
    }

    var addCircleBtn = document.getElementById("addCircle");
    var startBtn = document.getElementById("startAnim");
    var stopBtn = document.getElementById("stopAnim");

    var radiusWidth = 50;
    var interTime = 10;
    var defaultOption = {};
    defaultOption.x = 100;
    defaultOption.y = 100;

    if(isCanvasSupported){
        addCircle();
        setInterval(function(){
            if(pause){
                animateCircle();
            }
        }, interTime);
        canvas.addEventListener('click', function(e){
            console.log(e);
            var x;
            var y;
            var thisradius = (radiusWidth*Math.random());
            if (e.offsetX || e.offsetY) {
                x = e.offsetX;
                y = e.offsetY;
            }
            else {
                x = e.clientX + document.body.scrollLeft + document.documentElement.scrollLeft;
                y = e.clientY + document.body.scrollTop + document.documentElement.scrollTop;
            }
            x -= canvas.offsetLeft;
            y -= canvas.offsetTop;

        addCircle(x, y, thisradius);
        }, false);

        if(addCircleBtn){
            addCircleBtn.addEventListener('click', function(e){
                var thisradius = (radiusWidth*Math.random());
                addCircle(undefined, undefined, thisradius)
            }, false);
        }

        if(startBtn){
            startBtn.addEventListener('click', function(){
                pause = true;
            },false);
        }

        if(stopBtn){
            stopBtn.addEventListener('click', function(){
                pause = false;
            },false);
        }
    }else{
        alert('Canvas is not supported in you browser.')
    }

    function degree(value){
        return value * (Math.PI / 180);
    }

    function addCircle(x, y, radius){
        if(typeof x == 'undefined'){
            x = getRandomInt(1,canvas.width);
        }

        if(typeof y == 'undefined'){
            y = getRandomInt(1,canvas.height);
        }

        if(typeof radius == 'undefined'){
            radius = (radiusWidth*Math.random());
        }

        if(x < radius){
            x = radius;
        }else if(x > (canvas.width-radius)){
            x = (canvas.width-radius);
        }

        if(y < radius){
            y = radius;
        }else if(y > (canvas.height-radius)){
            y = (canvas.height-radius);
        }

        balls.push({x:x,
            xvc:(3*Math.random()),
            y:y,
            yvc:(3*Math.random()),
            radius:radius,
            color: 'rgba(' + (Math.random()*238).toFixed(0) + ', ' +
                (Math.random()*238).toFixed(0) + ', ' +
                (Math.random()*238).toFixed(0) + ', 1.0)'
        });
    }

    function animateCircle(){
        context.clearRect(0, 0, canvas.width, canvas.height);
        balls.forEach(function(balls){
            context.beginPath();
            context.arc(balls.x, balls.y, balls.radius, degree(0), degree(360), false);
            context.fillStyle = balls.color;
            context.closePath();
            context.fill();
            changePosition(balls);
        });
    }

    function changePosition(balls){
        if (balls.x + balls.xvc + balls.radius > canvas.width ||
            balls.x + balls.xvc - balls.radius < 0){
            balls.xvc = -balls.xvc;
        }

        if (balls.y + balls.yvc + balls.radius > canvas.height ||
            balls.y + balls.yvc - balls.radius  < 0) {
            balls.yvc= -balls.yvc;
        }

        balls.x += balls.xvc;
        balls.y += balls.yvc;
    }

    function getRandomInt (min, max) {
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }
}]);
